html,
body {
  margin: 0;
}

body {
  display: flex;
  min-width: 360px;
  background: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.01), rgba(0, 0, 0, 0.5)),
    url('./assets/images/background-tile.png');
  background-color: #222;
}

#root {
  display: inherit;
  width: 100%;
  min-height: 100vh;
  align-items: center;
}